.popup__input {
    padding: 0px 0 13px;
    margin: 0 36px 0;
    border: none;
    border-bottom: 1px solid rgb(0, 0, 0, 0.2);
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    outline: none;
}

.popup__input::placeholder {
    color: #C4C4C4;
}

@media (max-width: 879px) {
    .popup__input {
        margin: 0 22px 0;
    }
}