.gallery {
    margin-top: 51px;
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 17px;
    row-gap: 20px;
    max-width: 880px;
    padding: 0;
}

@media (max-width: 879px) {
    .gallery {
        grid-template-columns: auto;
        margin-top: 36px;
    }
}