.profile__avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-right: 30px;
    object-fit: cover;
    display: block;
    background-position: center;
    background-size: cover;
}

@media (max-width: 879px) {
    .profile__avatar {
        align-self: center;
        margin-right: 0px;
    }
}